		padding: 0 1rem;
		border-bottom: 1px solid #f0f1f2;
	}
	.footer-btnLink{
		padding: 2.46% 5.15% 0;
		text-align: center;
		color: #969ba3;
		overflow: hidden;
	}
	.footer-btnLink,
	.footer-btnLink ol{
		overflow: hidden;
	}
	.footer-btnLink li{
		width: 20%;
		float: left;
		padding: 0.5625rem 0;
	}
	.dark{
		color: #33373d;
	}
	.footer-copy{
		font-size: 0.875rem;
		font-weight: 300;
		padding-top: .25rem;
		padding-bottom: 1rem;
		text-align: center;
		color: #969ba3;
	}
	/* APP */
	.downApp{
		height: 3.75rem;
	    position: relative;
		display: block;
		box-sizing: border-box;
		padding: .75rem 6rem 0 4rem;
	}
	.downApp img{
		position: absolute;
		top: .75rem;
		width: 2.25rem;
		height: 2.25rem;
		left: 1rem;
	}
	.downApp h3{
		font-size: 1rem;
	}
	.downApp p{
		font-size: 0.8125rem;
		color: #969ba3;
	}
	.downApp span{
		height: 2rem;
		position: absolute;
		top: 0;
		right: 1rem;
		bottom: 0;
		margin: auto;
		font-size: .875rem;
		line-height: 2rem;
		display: inline-block;
		padding-right: 1.3em;
		padding-left: 1.3em;
		text-align: center;
		color: #fff;
		border-radius: 99px;
		background-color: #ed424b;
	}
	/*  */
	.Box-icon{
		position: fixed; 
		border: 0.0625rem solid;
		border-radius: 99px;
		cursor: pointer;
	}
	.readBox{
		width: 2rem;
		height: 2rem;
		padding: 0.5625rem;
		display: block;
		right: 20px; 
		bottom: 5rem;
		background: #ed424b;
		color: #fff;
		font-size: 0.8125rem;
		text-align: center;
	}
	.readBox a{
		color: #fff;
	}
	.backTop{
		width: 3.125rem;
		height: 3.125rem;
		right: 20px; 
		bottom: 1rem;
		background: #000;
		opacity: 0.6;
	}
	.backTop .icon{
		width: 1.375rem;
		height: 1.375rem;
		color: #fff;
		position: relative;
		left: 26%;
		top: 27%;
	}
	/*  */
	aside{
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		right: 0;
		z-index: 5;
	}
	.gray-box{
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0.6;
		background: #000;
	}
	.reading-box{
		height: 100%;
		position: absolute;
		left: 2.5rem;
		right: 0;
		background: #fff;
		overflow: hidden;
	}
	.content-one{
		line-height: 1.9375rem;
		padding: 18px 18px 0 18px;
		text-align: center;
		display: block;
	}
	.content-two{
		padding: 1.125rem;
	}
	
	.two-bookname{
		overflow: hidden;
	}
	.bookname-tit{
		float: left;
		font-size: 1.125rem;
		font-weight: bolder;
		margin-bottom: 0.1875rem;
	}
	.content-two p{
		color: #969ba3;
	}
	.bookname-toRead{
		float: right;
		margin-right: 0;
	}
	.bookname-toRead,
	.bookname-toRead .icon{
		font-size: 0.875rem;
	}
	.all-reading{
		font-size: 0.875rem;
		color: #969ba3;
		display: block;
		line-height: 3.125rem;
		text-align: center;
		border-top: 0.0625rem solid #f0f1f2;
		margin-top: 1.125rem;
	}
	.content-three{
		padding: 1.125rem;
		text-align: center;
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
	}
	.content-three-a{
		width: 60%;
		margin-bottom: 0.84375rem;
		text-decoration: none;
		color: #fff;
		background: #ED424B;
		border-radius: 99px;
		padding: 0 1.125rem;
		line-height: 2.5rem;
		display: inline-block;
		box-sizing: border-box;
	}
	/**/
	.fade-enter{
		width: 0;
	}
	.fade-enter-to{
		width: 60%;
	}
	.fade-enter-active{
		transition: all 0.5s;
	}
	/**/
	.fade-leave{
		width: 60%;
	}
	.fade-leave-to{
		width: 0;
	}
	.fade-leave-active{
		transition: all 0.5s;
	}
</style>



